.App {
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
}

.header {
	flex-basis: auto;
	background-color: #0e1a21;
	text-align: center;
	font-size: 2rem;
}

.game-container {
	flex-grow: 1;
	position: relative;
}

.footer {
	flex-basis: auto;
	background-color: #0e1a21;
}

.bg-plot-container, .layout-container {
	position: absolute !important;
	bottom: 0;
	left: 0;
	right: 0;
	top: 0;
}

.bg-plot-container {
	z-index: 1;
}

.plot {
	width: 100%;
	height: 100%;
}

.layout-container {
	z-index: 2;
	pointer-events: none;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap-reverse;
	align-content: stretch;
}

@media (max-aspect-ratio: 1/1) {
	.layout-container {
		flex-direction: column-reverse;
		flex-wrap: wrap;
	}
}

.guess-form * {
	pointer-events: all;
}

.guess-container {
	/*background-color: rgba(231, 4, 4, 0.16);*/
	align-self: baseline;
	padding: 1rem;
}

.guess-input, .guess-submit {
	margin-right: 0.5rem;
	font-size: 1.5rem;
	background-color: rgba(14, 26, 33, 0.77);
	color: #ececec;
	box-shadow: none;
	border-style: solid;
	border-width: 0.1rem;
	border-radius: 0.5rem;
	padding: 0.2rem;
	font-family: 'Space Grotesk', 'Roboto', sans-serif;
	backdrop-filter: blur(3px);
}

.guess-submit {
	height: 3rem;
}

.guess-input {
	height: 2rem;
	width: 15rem;
	margin-right: 0.5rem;
}

.safe-container {
	pointer-events: none;
	background-color: rgba(0, 255, 4, 0.1);
	border-radius: 1rem;
	margin: 1rem;
	flex-grow: 1;
}